@import '~semantic-ui-css/semantic.min.css';


.ui.inverted.segment.heading-image {
  background-image: url(./images/code.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  max-width: 100vw;
  &.desktop {
    min-height: 700px;
    padding: 1em 0em;
  }
  &.mobile {
    min-height: 350px;
    padding: 1em 0em;
  }
}
.column.project-card{
  padding-top: 5em;
  padding-bottom: 5em;
  display: flex;
  justify-content: center;
}
